.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  color: #242423;
  background: linear-gradient(87deg, #172b4d 0, #1a174d 100%) !important;
}

.header__title {
  font-family: sans-serif;
  font-size: 30px;
  color: white;
  transition: ease 0.3s;
}

@media only screen and (min-width: 768px) {
  .header {
    flex-direction: row;
  }
}
