.search__input {
  padding: 0.5rem 0.5rem;
  border-radius: 10px;
  background-color: #242423;
  border: none;
  font-size: 1rem;
  color: white;
  outline: none;
  padding-left: 20px;
}

.search__input::placeholder {
  color: white;
  text-align: left;
  opacity: 0.7;
}

@media screen and (max-width: 480px){
  .search__input{
    width: 300px;
    margin-left: 0;

  }
}
